
.landing {
  display: flex;
  flex-direction: column;
  background-color: var(--smokle);
}

.landing .intro-section {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5rem;
    height: 100vh;
    margin-top: 5vh;
}

.intro-section .intro-text {
    display: flex;
    flex-direction: column;
    width: 30vw;
}

.intro-text h3 {
    font-size: 2rem;
    color: var(--darkgray);
}

.intro-text h1 {
    width: 80%;
    font-size: 6rem;
    color: var(--black);
}

.intro-text p {
    font-size: 1.25rem;
    color: var(--darkgray);
}

.landing .intro-img {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}

.landing .intro-img img {
    height: 700px;
    border-radius: 1rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.landing .stacked-img {
    display: flex;
    flex-direction: column;
    height: 700px;
    justify-content: space-between;
}

.landing .stacked-img img {
    height: 330px;
}

