
.add-ons {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 700px;
}

.add-ons .add-ons-section {
    display: flex;
    flex-direction: column;
    width: 80%;
    height: 100%;
    margin-top: 3rem;
}

.add-ons-section .add-ons-text h1 {
    font-size: 2rem;
    font-weight: 300;
    color: var(--darkgray);
    margin-bottom: 2rem;
}

.add-ons-section .add-ons-scroller {
    display: flex;
    overflow-x: scroll;
    gap: 2rem;
    width: 100%;
    height: auto;
}

.add-ons-scroller .add-on-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 200px;
    height: 400px;
    border-radius: 1rem;
    border: 1px solid var(--gray);
    padding: 5px;
    text-decoration: none;
}

.add-ons-scroller .add-on-card:hover {
    background-color: var(--smoke);
}

.add-on-card .add-on-image img {
    width: 200px;
    height: auto;
    object-fit: cover;
    border-radius: 1rem;
}

.add-on-card .add-on-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1rem;
    text-align: center;
    gap: 1rem;
}

.add-on-card .add-on-details h3 {
    font-size: 1.25rem;
    font-weight: 700;
    color: var(--black);
}

.add-on-card .add-on-details p {
    font-size: 1rem;
    font-weight: 300;
    color: var(--darkgray);
}