.featured .featured-section {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;
}

.featured-section .featured-vertical img {
    height: 600px;
    border-radius: 1rem;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    margin-top: 5vh;
    transition: opacity 0.8s ease-in-out;
    opacity: 0;
    animation: fadeIn 1s forwards;
}

.featured .product-sliders {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 60vw;
    height: 800px;
}

.product-sliders .slider-title {
    color: var(--darkgray);
    font-size: 2rem;
    margin-right: auto;
    margin-left: 2rem;
}

/* Slider container */
.slider {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 300px;
    background: linear-gradient(135deg, var(--smoke), var(--gray));
    border-radius: 1rem;
    overflow: hidden;
    position: relative;
    padding: 10px 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Slider wrapper for sliding effect */
.slider-wrapper {
    width: 80%;
    overflow: hidden;
}

/* Slider track moves items */
.slider-track {
    display: flex;
    transition: transform 0.5s ease-in-out;
    gap: 1rem;
    width: 940px;
    cursor: pointer;
}

.slider-track img {
    width: 200px;
    height: 150px;
    object-fit: contain;
    background-color: var(--white);
}

/* Product items */
.product-item {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 250px;
    height: 250px;
    background-color: var(--white);
    border-radius: 1rem;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
    flex-shrink: 0;
    text-decoration: none;
    color: var(--black);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Product item hover effect */
.product-item:hover {
    background-color: var(--smoke);
    box-shadow: 0 10px 15px rgba(0, 0, 0, 0.15);
}

/* Name and price divider */
.product-item .item-divider {
    width: 50px;
    height: 5px;
    background-color: var(--orange);
}

/* Slider navigation buttons */
.slider-btn {
    background: none;
    border: none;
    font-size: 3rem;
    cursor: pointer;
    color: var(--darkgray);
    transition: 0.3s;
}

.slider-btn:hover {
    color: var(--orange);
    transform: scale(1.1);
}


@keyframes fadeIn {
    to {
        opacity: 1;
    }
}