
.items-field {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    padding: 20px;
}
  
.product-card {
    background-color: #fff;
    border: 1px solid #eaeaea;
    border-radius: 5px;
    overflow: hidden;
    transition: box-shadow 0.3s ease;
    cursor: pointer;
    text-decoration: none;
}
  
.product-card:hover {
    box-shadow: 0 2px 8px rgba(0,0,0,0.15);
}
  
.product-image img {
    width: 100%;
    display: block;
}
  
.product-details {
    padding: 15px;
    text-align: center;
}
  
.product-name {
    font-size: 1.1rem;
    margin: 0 0 10px;
    color: var(--black);
}
  
.product-price {
    font-size: 1rem;
    color: var(--darkgray);
}
  