
.item {
    display: flex;
    gap: 20vw;
    padding-top: 120px;
    margin: 0 15vw;
}

.item .item-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 25vw;
}

.item .item-img img {
    height: 800px;
    width: auto;
    object-fit: contain;
    border-radius: 4px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.item .item-text {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 25vw;
}

.item .item-text .item-info {
    display: flex;
    flex-direction: column;
}

.item .item-text .item-info h1 {
    font-size: 2.25rem;
    font-weight: 300;
    margin-bottom: 0px;
}

.item .item-text .item-info h2 {
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 20px;
}

.item .item-text .item-info p {
    font-size: 1.25rem;
    font-weight: 300;
}

.item .item-text .item-size h3 {
    font-size: 1.5rem;
    font-weight: 300;
    margin-bottom: 1rem;
}

.item .item-text .item-size .size-options {
    display: flex;
    flex-wrap: wrap;
    gap: 0.25rem;
}

.item .item-text .item-size .size-options label {
    display: inline-block;
    position: relative;
    cursor: pointer;
}

.item .item-text .item-size .size-options input[type="radio"] {
    position: absolute;
    opacity: 0;
    pointer-events: none;
}

.item .item-text .item-size .size-options .size-label {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ccc;
    color: var(--darkgray);
    width: 150px;
    height: 75px;
    font-size: 2rem;
    line-height: 1.2;
    border-radius: 4px;
    transition: border-color 0.2s, background-color 0.2s;
    user-select: none;
}

.item .item-text .item-size .size-options .size-label:hover {
    border-color: var(--darkgray);
}

.item .item-text .item-size .size-options input[type="radio"]:checked + .size-label {
    border-color: var(--darkgray);
    background-color: var(--gray);
    color: var(--black);
}

.item-text .item-button {
    width: 100%;
    height: 70px;
    background-color: var(--black);
    color: var(--white);
    font-size: 1.5rem;
    cursor: pointer;
}

.item .notification {
    position: fixed;
    top: 130px;
    right: 20px;
    background-color: var(--darkgray);
    color: var(--white);
    padding: 1rem 1.5rem;
    border-radius: 5px;
    opacity: 1;
    z-index: 1000;
    transition: opacity 0.5s ease-in-out;
}
