@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  scrollbar-color: var(--darkgray) var(--smoke);
}

:root {
  --black: #000;
  --darkgray: #555;
  --gray: #ddd;
  --smoke: #eee;
  --white: #fff;
  --orange: #f70;
  --green: #50C878;
  --darkgreen: #4F7942;
}