
.about {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 150px;
    background-color: var(--darkgray);
}

.about .terms {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    color: var(--smoke);
    font-size: 1rem;
    font-weight: 500;
}

.about .terms p {
    cursor: pointer;
}

.about .about-us {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    color: var(--smoke);
    font-size: 1rem;
    font-weight: 500;
}

.about .about-us p {
    cursor: pointer;
}

.about .socials {
    display: flex;
    gap: 2rem;
}

.about .socials img {
    height: 32px;
    width: 32px;
    cursor: pointer;
}