@import url('https://fonts.googleapis.com/css2?family=Meow+Script&display=swap');

.navbar {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    position: fixed;
    height: 100px;
    width: 100vw;
    background-color: var(--white);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
    z-index: 10;
}

.navbar .navbar-section {
    margin: auto;
}

.navbar .navbar-links {
    display: flex;
    align-items: center;
    gap: 5rem;
    font-weight: 600;
    
}

.navbar .navbar-links a {
    text-decoration: none;
    color: var(--darkgray);
}

.navbar .navbar-links a:hover {
    color: var(--black);
}

.logo {
    font-family: "Meow Script", cursive;
    font-size: 3rem;
    cursor: pointer;
    text-decoration: none;
    color: var(--black);
    font-weight: 600;
}

.navbar .navbar-cart {
    display: flex;
    align-items: center;
}

.navbar .search-bar {
    display: flex;
    width: 200px;
    height: 40px;
    background-color: var(--white);
    border-radius: 0.5rem;
    align-items: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
}

.navbar .search-bar img {
    width: 24px;
    height: 24px;
    margin-left: 8px;
    cursor: pointer;
}

.navbar .search-bar input {
    width: 95%;
    height: 100%;
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 1rem;
    font-weight: 500;
    color: var(--darkgray);
    margin-left: 5px;
}

.navbar .cart-img {
    width: 32px;
    height: 32px;
    margin-left: 2rem;
    cursor: pointer;
}

.navbar .item-count {
    width: 24px;
    height: 24px;
    background-color: var(--orange);
    color: var(--white);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 32px;
}


.navbar .hidden {
    display: none;
}