.previews-container {
    position: relative;
    width: calc(1920px / 2);
    height: calc(1080px / 2);
    overflow: hidden;
    margin: auto;
    border-radius: 10px;
    padding-top: 120px;
    background-color: var(--white);
}
  
.previews-container .previews-imgs {
    display: flex;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100%;
}
  
.previews-container .previews-img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}
  
.previews-container .nav-button {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 50px;
    height: 50px;
    top: 60%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    cursor: pointer;
    font-size: 24px;
    border-radius: 50%;
    z-index: 10; /* Ensure buttons are above other elements */
}
  
.previews-container .nav-button:hover {
    background: rgba(0, 0, 0, 0.8);
}
  
.previews-container .left {
    left: 10px;
}
  
.previews-container .right {
    right: 10px;
}
  