.filters {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    background-color: var(--white);
    border-radius: 8px;
    margin-top: 20px;
}

.filters .sort-by {
    position: relative;
    margin-right: 1rem;
}

.filters .sort-by button {
    background-color: var(--darkgray);
    color: var(--white);
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.filters .sort-by button:hover {
    background-color: var(--black);
    transform: scale(1.05);
}

.filters .sort-by .dropdown-sort-by {
    display: none;
    position: absolute;
    top: 100%;
    right: 0;
    background-color: var(--smoke);
    border: 1px solid var(--lightgray);
    border-radius: 4px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 10;
    min-width: 200px;
    padding: 0.5rem 0;
    list-style: none;
}

.filters .sort-by .dropdown-sort-by li {
    padding: 10px 16px;
    font-size: 0.9rem;
    color: var(--darkgray);
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease;
}

.filters .sort-by .dropdown-sort-by li:hover {
    background-color: var(--gray);
    color: var(--black);
}

.filters .sort-by:hover .dropdown-sort-by {
    display: block;
}