
.basket {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    margin-bottom: 50px;
    padding-top: 100px;
}

.basket h1 {
    font-size: 3rem;
    font-weight: 300;
    color: var(--darkgray);
}

.basket .basket-content {
    display: flex;
    margin-top: 2rem;
    gap: 10vw;
    width: 70%;
}

.basket .basket-items {
    display: flex;
    flex-direction: column;
    width: 75%;
    gap: 2rem;
}

.basket .basket-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 225px;
    gap: 1rem;
    align-items: center;
    padding: 1rem;
    border-bottom: 2px solid var(--gray);
}

.basket .basket-item img {
    width: 150px;
    height: auto;
    object-fit: contain;
    border-radius: 4px;
}

.basket .basket-item .basket-item-text-img {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
}

.basket .basket-item .basket-item-info {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.basket .basket-item .basket-item-info .main-info a {
    text-decoration: none;
    color: var(--black);
}

.basket .basket-item .basket-item-info .other-info {
    color: var(--darkgray);
}

.basket .basket-item .basket-item-info h2 {
    font-size: 1.5rem;
    font-weight: 300;
    color: var(--black);
}

.basket .basket-item .basket-item-del-add {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: end;
    height: 100%;
}

.basket .basket-item .basket-item-del-add img {
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.basket .basket-item .basket-item-del-add .ammount-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
}

.basket .basket-content .checkout {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 25%;
}

.basket .basket-content .checkout .prices {
    display: flex;
    gap: 0.5rem;
    justify-content: space-between;
}

.basket .basket-content .checkout .prices .price-title {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 0.5rem;
}

.basket .basket-content .checkout .prices .price-calc {
    display: flex;
    flex-direction: column;
    align-items: end;
    gap: 0.5rem;
}

.basket .basket-content .checkout .prices h2 {
    font-size: 1.25rem;
    font-weight: 300;
    color: var(--darkgray);
}

.basket .basket-content .checkout .prices h1 {
    font-size: 1.75rem;
    font-weight: 700;
    color: var(--black);
}

.basket .basket-content .checkout .checkout-btn {
    background-color: var(--green);
    color: var(--white);
    border: none;
    border-radius: 4px;
    padding: 1rem;
    font-size: 1.25rem;
    font-weight: 700;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}

.basket .basket-content .checkout .checkout-btn:hover {
    background-color: var(--darkgreen);
}


.basket-cart .empty-basket {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    height: 100vh;
    color: var(--darkgray);
}

.basket-cart .empty-basket h1 {
    font-size: 3rem;
    font-weight: 300;
}

.basket-cart .empty-basket p {
    font-size: 1.25rem;
    font-weight: 300;
}